<template>
    <div>  
        <div class="loginNav">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">报修</p>
        </div>
        <div class="content c666 f28" style="padding:1rem 0.3rem 1.7rem;">
            <div class="type">
                <p class="kind f32"><img src="../../../assets/message/type.png"/>设备类别</p>
                <ul>
                    <li @click="fix(index,item)" v-for="(item,index) in list" :key="index" :style="{margin:(index+1)%4==0&&index!=0?'0':'0 .3rem .3rem 0',background:item.select?'#7C88EE':'#F3F3F3'}">{{item.labelName}}</li>
                </ul>
            </div>
            <div>
                <p class="kind f32"><img src="../../../assets/message/type.png"/>所在班级</p>
                <div class="selectClass" @click="selectClass">
                    <p class="c333 pr-30 pl-30">{{info.class?info.class:'选择班级'}}</p>
                    <img :src="click?down:up" alt="">
                    <ul :style="{height:click?classType.length*0.78+'rem':'0'}">
                        <li @click="chooseClass(item)" v-for="(item,index) in classType" :key="index">{{item.name}}</li>
                    </ul>
                </div>
                <textarea class="mt-30 bs c333" placeholder="请输入设备报修原因......" v-model="info.reason"></textarea>
            </div>
        </div>
        <div class="centerBtn f26 cfff text-center" @click="issue">提 交</div>
    </div>
</template>

<script>

export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            up:require('../../../assets/message/up.png'),
            down:require('../../../assets/message/down.png'),
            click:false,
            list:[],//设备列表
            classType:[
                {
                    id:'',
                    name:'三年一班',
                },{
                    id:'',
                    name:'三年二班',
                },{
                    id:'',
                    name:'三年三班',
                },{
                    id:'',
                    name:'三年九班',
                },{
                    id:'',
                    name:'三年十八班',
                },{
                    id:'',
                    name:'三年二十一班',
                },
            ],
            info:{
                type:'桌子'
            }
        }
    },
    methods : {
        returns(){
            this.$router.go(-1);//返回上一层
        },
        lookDetail(index){
            var list=this.list;
            list[index].isSee=true;
            this.list=list;
            this.$router.push({path:'/leaveDetail',query:{active:this.active}})
        },
        fix(index,item){
            var list=this.list;
            if(!list[index].select){
                for(var i in list){
                    list[i].select=false
                }
                list[index].select=true;
            }
            this.list=list;
            this.info.type=item.name;
        },
        selectClass(){
            this.click=!this.click;
        },
        chooseClass(item){
            this.info.class=item.name;
        },
        issue(){
            console.log(this.info);
            if(!this.info.class){
                this.$toast.fail('请选择班级');
            }else if(!this.info.reason){
                this.$toast.fail('请输入设备报修原因');
            }
        },
        getLabelList(){
            this.$http({url:this.$api.message.getLabelList},{schoolId:JSON.parse(localStorage.getItem('zhxy_user')).schoolId}).then(data => {
                console.log(data);
                if(data.code == 0){
                    for(var i in data.data){
                        if(i == 0){
                            data.data[i].select=true;
                        }else{
                            data.data[i].select=false;
                        }
                    }
                    this.list = data.data;
                }
            })
        }
    },
    created() {
        this.getLabelList()
    },
}
</script>

<style scoped>
     /* 修改组件样式 */
    .loginNav{
        position: fixed;
    }
    .van-tabbar-item--active {color: #8a9cff;}
    .content{
        padding:.3rem;
    }
    .kind{
        padding:.2rem 0 .5rem;
    }
    .kind img{
        width:.08rem;
        height:.32rem;
        margin-right:.25rem;
        vertical-align: middle;
    }
    .type li{
        width:1.5rem;
        height:.6rem;
        line-height:.6rem;
        text-align: center;
        display: inline-block;
        color:#fff;
        border-radius: .1rem;
    }
    .selectClass{
        position: relative;
        line-height: .78rem;
    }
    .selectClass img{
        position: absolute;
        width:.3rem;
        height: .3rem;
        right:.3rem;
        top:.24rem;
    }
    .selectClass p{
        border-radius: .1rem;
        background: #F3F3F3;
    }
    .selectClass ul{
        position: absolute;
        left:0;
        top:.78rem;
        text-align: center;
        width:100%;
        transition:height .2s;
        overflow: hidden;
        background: #F1F1F1;
        border-radius: .1rem;
    }
    textarea{
        width: 100%;
        height:4.8rem;
        background: #F3F3F3;
        border:0;
        border-radius: .1rem;
        padding: .3rem;
    }
    .centerBtn{
        height: 0.9rem;
        border-radius: 0.9rem; 
        background: linear-gradient(to bottom right, #82cbff , #7b7ceb);
        line-height: 0.9rem; 
        box-shadow: 0 0 10px #f0f9ff;
        position: fixed;
        bottom: 0.5rem;
        left:1rem;
        right:1rem;
    }
</style>




